@import '../../../asset/style/variable';

.logo {
  width: 24px;
  height: 24px;
  border-radius: $border-radius;
}

.title {
  display: inline-block;
  margin: 0 8px;
  text-decoration: unset;
  color: $snow;
  &:hover {
    color: $blue;
  }
}

.button {
  border: none;
  font-size: $font-size-xl;
  background: transparent;
  color: $snow;
  &:hover {
    color: $blue;
  }
}

.check {
  color: $green;
}
